@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar {
    background-color: #1da1f2;
    display: flex;
    font-size: 1.2rem;
    height: 80px;
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 20px;
}

.navbar-logo {
    color: #ffffff;
    cursor: pointer;
    text-decoration: none;
    font-family: "Anton", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 2rem;
    display: flex;
    align-items: center;
}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
}

.nav-item {
    height: 60px;
}

.nav-links {
    color: #ffffff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 400;
    font-style: normal;
}

.nav-links:hover {
    color: #cccccc;
    transition: all 0.2s ease-out;
}

.nav-links.active {
    font-weight: bold;
    text-decoration: underline;
}
.homepage {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form-container {
    font-family: Arial, sans-serif;
    max-width: 400px;
    margin: 0 auto;
    padding: 30px;
    text-align: center;
}

.form-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.nav-link {
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    color: black;
}

.form-content .logo {
    font-size: 40px;
    margin-bottom: 20px;
}

.form-content h1 {
    font-size: 20px;
    margin-bottom: 30px;
}

.form {
    text-align: left;
}

.form-label {
    font-size: 12px;
    margin-bottom: 5px;
    display: block;
    color: gray;
}

.form-input::placeholder {
    font-style: italic;
}

.form-input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 14px;
    box-sizing: border-box;
}

.password-container {
    display: flex;
    align-items: center;
}

.form-button {
    width: 100%;
    padding: 10px;
    background-color: #ddd;
    border: none;
    font-size: 14px;
    color: white;
    cursor: not-allowed;
    transition: all 0.3s ease;
}

.form-button.active {
    background-color: #1da1f2;
    cursor: pointer;
}

.form-button.active:hover {
    background-color: #007BFF;
}
